* {
  box-sizing: border-box;
  transition: none 0.2s;
}

.body-div {
  height: 100%;
  min-height: 100vh;
  overflow-x: clip;
  display: inherit;
  flex-direction: inherit;
  text-align: inherit;
}

.full {
  position: relative;
  width: 100%;
  height: 0;
  padding-bottom: 56.25%; /* 16:9 aspect ratio */
}

.full iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  max-width: none !important;
}
